<template lang="pug">
  .overview
    .today-overview
      h2 今日总览
      .overview-left.fl
        h3 当前存续金额（元）
        .overview-left-middle
          i(class="icon-icomoon icon-overview")
          span {{balance.total | ktCurrency('')}}
        .overview-left-down
          .Principal.fl
            span 存续本金
              em {{balance.principal | ktCurrency('')}}
          .Interest.fr
            span 存续利息
              em {{balance.interest | ktCurrency('')}}
      .overview-right.fr
        h3 今日存量产品占比分析
        .overview-right-middle
          kt-pie-echart(:chart-option="pieChartOption")

    .today-square
      h2 近期清算
        el-tooltip(effect="dark",placement="right")
          .Prompt(slot="content") 展示近5个工作日需资金清算的所有产品，包括近5个工作日流入资金的入账确认以及近5日流出资金的划款确认 <br> 待执行-清算数据正常可以进行清算执行操作确认<br> 不可执行-清算数据尚未更新或者异常导致无法执行清算确认 <br> 已执行-已经进行过清算执行操作确认 <br> 已过期-当前时间已经晚于需确认清算执行的最晚时限
          i(class="icon-icomoon icon-explain")
        .information.fr
          span {{summary.executed}}
          em 条 已执行，
          span {{summary.pending_execute}}
          em 条 待执行，
          span {{summary.cant_execute}}
          em 条 不可执行，
          span {{summary.expired}}
          em 条 已过期
      .overview-table
        .table-container(v-for="(item,key) in virtualAssets", v-if="item.data.length")
          table
            thead
              tr
                th {{item.date}}
                th
          .table-body
            table
              tbody(v-for="product in item.data")
                tr
                  td(:title="product.consignee",:rowspan="product.data.length")
                    span {{product.consignee}}
                  td
                    table
                      tbody
                        tr.first_trn(v-for="data in product.data",@click="toDetails(data)")
                          td.first_tdn(:title="data.name | ktCurrency")
                            span(v-if="data.name") {{data.name}}
                              em.red-color {{product.inflow | ktCurrency}}
                          td.minW(:title="data.inflow | ktCurrency")
                            span(v-if="data.inflow_desc") {{data.inflow_desc}}
                              em.red-color {{data.inflow | ktCurrency}}
                          td.minW(:title="data.outflow | ktCurrency")
                            span(v-if="data.outflow_desc") {{data.outflow_desc}}
                              em.green-color {{data.outflow | ktCurrency}}
                          td.minW(:title="data.net_cash_flow | ktFlow  | ktCurrency")
                            span {{data.net_cash_flow >= 0 ? '净流入' : '净流出'}}
                              em(:class="[data.net_cash_flow >= 0 ? 'red-color' : 'green-color']") {{data.net_cash_flow | ktFlow | ktCurrency }}
                          td
                            span.bg-color(v-if="data.execute_method") {{data.execute_method}}
                          td.implement.status-column
                            i.icon-icomoon(:class="data.execute_status | excuteStatusIcon")
                            em.em-implement {{data.execute_status}}
                          td
                            span(v-show="data.execute_status === '待执行' ? true : false") 结算时限:
                              em {{data.due_at}}
    .today-square
      h2 今日更新
        el-tooltip(effect="dark",placement="right")
          .Prompt(slot="content") 展示今日需数据更新的所有产品 <br> 已更新-本期更新数据已经传输和处理完毕 <br> 待更新-本期应该有更新数据但尚未开始传输 <br> 更新中-本期更新数据正在传输或处理中 <br> 异常-当前时间已经晚于数据更新的最晚截止时间但仍未传输或处理完毕
          i(class="icon-icomoon icon-explain")
        .information.fr
          span {{updateSummary.updated}}
          em 条 已更新，
          span {{updateSummary.pending_update}}
          em 条 待更新，
          span {{updateSummary.updating}}
          em 条 更新中，
          span {{updateSummary.abnormal}}
          em 条 异常
      .overview-table
        .table-container(v-for="(item,key) in updateAssets", v-if="item.data.length")
          table
            thead
              tr
                th {{item.date}}
                th
          .table-body
            table
              tbody(v-for="product in item.data")
                tr
                  td(:title="product.consignee",:rowspan="product.data.length")
                    span {{product.consignee}}
                  td
                    table
                      tbody
                        tr.first_trn(v-for="data in product.data",@click="toDetails(data)")
                          td.first_tdn(:title="data.name | ktCurrency")
                            span(v-if="data.name") {{data.name}}
                              em.red-color {{product.inflow | ktCurrency}}
                          td.minW(:title="data.inflow | ktCurrency")
                            span(v-if="data.inflow_desc") {{data.inflow_desc}}
                              em.red-color {{data.inflow | ktCurrency}}
                          td.minW(:title="data.outflow | ktCurrency")
                            span(v-if="data.outflow_desc") {{data.outflow_desc}}
                              em.green-color {{data.outflow | ktCurrency}}
                          td.minW(:title="data.net_cash_flow | ktFlow  | ktCurrency")
                            span {{data.net_cash_flow >= 0 ? '净流入' : '净流出'}}
                              em(:class="[data.net_cash_flow >= 0 ? 'red-color' : 'green-color']") {{data.net_cash_flow | ktFlow | ktCurrency }}
                          td
                            span.bg-color(v-if="data.execute_method") {{data.execute_method}}
                          td.implement.status-column
                            i.icon-icomoon(:class="data.update_status | updateStatusIcon")
                            em.em-implement {{data.update_status}}
                          td
                            span(v-show="data.execute_status === '待执行' ? true : false") 结算时限:
                              em {{data.due_at}}
    .today-square
      h2 趋势图
      .trend-chart
        .trend-product.fl
          h3 存量产品金额波动趋势
          .crumbs
            ul
              li(v-for="stocktrend in stocktrends", @click="goTrend(stocktrend)" ,:class="{active:stocktrend == stockName}") {{stocktrend}}
          .trend-chart
            kt-bar-chart(:chart-option="stockChartOption")
        .trend-stock.fr
          h3 交易所近期资金流动趋势
          .capital-chart
            kt-line-chart(:chart-option="lineChartOption")
</template>

<script>
import _ from 'lodash'
import moment from 'moment'
import {
  subsist,
  stock,
  liquidation,
  updatedProducts,
  stockTrend,
  capitalTrend
} from '../../common/resources.js'
import KtPieEchart from '../../components/kt-pie-echart.vue'
import ktBarChart from '../../components/kt-bar-echart.vue'
import ktLineChart from '../../components/kt-line-echart.vue'
import {
  Tooltip,
  Loading
} from 'element-ui'
import exMixin from './mixin.js'
export default {
  mixins: [exMixin],
  components: {
    ktLineChart,
    ktBarChart,
    KtPieEchart,
    ElTooltip: Tooltip
  },

  methods: {

    // 查看单个产品详情总览
    toDetails(product) {
      this.$router.push({
        name: 'productDashboard',
        params: {
          id: product.id,
          name: product.name
        }
      })
    },

    // 当前存续金额（元）
    subsistGet() {
      return subsist.get().then(res => res.json()).then(data => {
        this.balance = data.balance
      })
    },

    // 今日存量产品占比分析
    stockGet() {
      return stock.get().then(res => res.json()).then(data => {
        this.pieChartOption = _.merge({}, this.pieChartOption, {
          legend: {
            data: _.map(data.balance.by_consignee, 'consignee')
          },
          series: [{
            name: '',
            type: 'pie',
            center: ['70%', '50%'],
            radius: ['50%', '80%'],
            avoidLabelOverlap: false,
            label: {
              normal: {
                show: false,
                position: 'center'
              },
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: '25',
                  fontWeight: 'bold'
                }
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: _.map(data.balance.by_consignee, v => {
              return {
                name: v.consignee,
                value: v.balance
              }
            })
          }]
        })
      })
    },
    //查找各平台的chart
    goTrend(platform) {
      this.collectivityChart(_.find(this.balanceTrends, v => v.consignee === platform).data)
      this.stockName = platform
    },
    // 存量产品金额波动趋势
    collectivityChart(data) {
      this.stockChartOption = _.mergeWith({}, this.stockChartOption, {
        xAxis: {
          data: _.map(data, v => moment(v.date).format('MM.DD'))
        },
        series: [{
          name: '存续本金',
          type: 'bar',
          stack: '广告',
          data: _.map(data, 'principal'),
          barMaxWidth: 40
        }, {
          name: '存续利息',
          type: 'bar',
          stack: '广告',
          data: _.map(data, 'interest'),
          barMaxWidth: 40
        }]
      }, (oldOpt, newOpt) => {
        if (_.isArray(newOpt)) {
          return newOpt
        }
      })
    },
    //存量产品金额波动趋势
    stockTrendGet() {
      return stockTrend.get().then(res => res.json()).then(data => {
        this.balanceTrends = data.balance_trends.by_consignee
        this.stocktrends = _.map(this.balanceTrends, 'consignee')
        this.goTrend('总体')
        this.stockName = '总体'
      })
    },
    //近期资金流动趋势
    capitalTrendGet() {
      return capitalTrend.get().then(res => res.json()).then(res => {
        this.lineChartOption = _.merge({}, this.lineChartOption, {
          legend: {
            data: ['申购', '到期', '赎回', '发行', '净现金流']
          },
          xAxis: {
            data: _.map(res.fund_trends, v => moment(v.date).format('MM-DD'))
          },
          series: [{
            name: '申购',
            type: 'line',
            data: _.map(res.fund_trends, v => v.subscription),
            color: ['#424853'],
            symbolSize: 6,
            symbol: 'circle'
          }, {
            name: '到期',
            type: 'line',
            data: _.map(res.fund_trends, v => v.settlement),
            color: ['#19b8bc'],
            symbolSize: 6,
            symbol: 'circle'
          }, {
            name: '赎回',
            type: 'line',
            data: _.map(res.fund_trends, v => v.redeemed),
            color: ['#41a5d7'],
            symbolSize: 6,
            symbol: 'circle'
          }, {
            name: '发行',
            type: 'line',
            data: _.map(res.fund_trends, v => v.issurance),
            color: ['#ad74d5'],
            symbolSize: 6,
            symbol: 'circle'
          }, {
            name: '净现金流',
            type: 'bar',
            data: _.map(res.fund_trends, v => v.net_cash_flow),
            color: ['#d1d2d4'],
            symbolSize: 6,
            barMaxWidth: 50,
            // barGap: 50,
            symbol: 'circle'
          }]
        })
      })
    },
    // 今日清算
    liquidationGet() {
      return liquidation.get().then(res => res.json()).then(data => {
        this.summary = data.summary
        this.virtualAssets = data.virtual_assets
      })
    },

    // 今日更新
    updatedProductsGet() {
      return updatedProducts.get({
        page: 1,
        per_page: 100
      }).then(res => res.json()).then(data => {
        this.updateSummary = data.summary
        this.updateAssets = data.virtual_assets
      })
    }
  },

  mounted() {
    this.instLoading = Loading.service({
      target: '.overview'
    })

    Promise.all([
      this.subsistGet(),
      this.stockGet(),
      this.liquidationGet(),
      this.updatedProductsGet(),
      this.stockTrendGet(),
      this.capitalTrendGet()
    ]).then(() => {
      this.instLoading.close()
    }).catch(res => {
      this.instLoading.close()
    })
  },

  data() {
    return {
      balance: '',
      pieChartOption: {},
      lineChartOption: {},
      summary: '',
      virtualAssets: '',
      updateAssets: '',
      updateSummary: '',
      updateVas: '',
      stocktrends: '',
      stockName: '',
      stockChartOption: {
        legend: {
          data: [{
            name: '存续本金'
              // icon: 'circle' //示例图标设为圆形
          }, {
            name: '存续利息'
              // icon: 'circle'
          }]
        },
        xAxis: {
          type: 'category',
          splitLine: {
            show: false
          },
          axisTick: {
            show: false
          }
        }
      }
    }
  }
}
</script>

<style lang="scss" scope>
.overview {
  background: #ecf1f7;
  h2 {
    margin-bottom: 10px;
    font-size: 17px;
  }
}

.today-overview {
  overflow: hidden;
  .overview-left,
  .overview-right {
    border-radius: 4px;
    background: #fff;
    padding: 10px;
    width: 49.5%;
  }
  .overview-left {
    h3 {
      background: #ecf1f7;
      height: 40px;
      line-height: 40px;
      font-size: 14px;
      text-indent: 10px;
      border-radius: 4px;
    }
    .overview-left-middle {
      height: 164px;
      line-height: 164px;
      text-align: center;
      border-bottom: 1px solid #ecf1f7;
      i {
        font-size: 57px;
        color: #eef1f3;
        display: inline-block;
        vertical-align: middle;
      }
      span {
        position: relative;
        font-size: 35px;
        color: #66cebe;
        margin-left: 35px;
        margin-top: 3px;
        vertical-align: middle;
        &:before {
          content: "￥";
          font-size: 30px;
          color: #c4c4c4;
          margin-right: 10px;
          margin-top: 5px;
        }
      }
    }
    .overview-left-down {
      height: 72px;
      line-height: 72px;
      overflow: hidden;
      padding: 0 10px;
      .Principal {
        span {
          font-size: 14px;
        }
        em {
          font-style: normal;
          color: #66cebe;
          margin-left: 8px;
          &:before {
            content: "￥";
            font-size: 13px;
            color: #c4c4c4;
            margin-right: 7px;
          }
        }
      }
      .Interest {
        span {
          font-size: 14px;
        }
        em {
          font-style: normal;
          color: #66cebe;
          margin-left: 8px;
          &:before {
            content: "￥";
            font-size: 13px;
            color: #c4c4c4;
            margin-right: 7px;
          }
        }
      }
    }
  }
  .overview-right {
    h3 {
      background: #ecf1f7;
      height: 40px;
      line-height: 40px;
      font-size: 14px;
      text-indent: 10px;
      border-radius: 4px;
    }
    .overview-right-middle {
      height: 237px;
    }
  }
}

.today-square {
  margin-top: 20px;
  h2 {
    i {
      font-size: 15px;
      color: #b4becf;
      margin-left: 5px;
    }
    .information {
      font-size: 13px;
      span {
        color: #54c9b8;
      }
      em {
        font-style: normal;
      }
    }
  }
  .first_trn {
    border-bottom: 1px solid #b4becf;
    &:last-child {
      border-bottom: none;
    }
  }
  .first_tdn{
  width:220px!important;
}
}

.overview-table {
  // max-height: 500px;
  // overflow-y: auto;
}

//趋势图
.trend-chart {
  overflow: hidden;
  .trend-product,
  .trend-stock {
    position: relative;
    background: #fff;
    border-radius: 4px;
    padding: 10px;
    width: 49.5%;
    height: 400px;
    h3 {
      height: 40px;
      width: 100%;
      line-height: 40px;
      color: #55627b;
      font-size: 15px;
      background: #ecf1f7;
      padding: 0 15px;
      border-radius: 4px 4px 0 0;
    }
  }
  .crumbs {
    overflow-x: auto;
    width: 100%;
    text-align: center;
    position: absolute;
    left: 0;
    right: 0;
    top: 62px;
    z-index: 9;
    ul {
      display: inline-block;
      // width: 100000px;
    }
    li {
      list-style: none;
      display: inline-block;
      padding: 5px 10px;
      font-size: 12px;
      color: #55627b;
      cursor: pointer;
      &:before {
        content: '';
        width: 8px;
        height: 8px;
        background: #333;
        border-radius: 100%;
      }
      &.active {
        color: #35cec3;
      }
    }
  }
  .trend-chart {
    width: 100%;
    height: 350px;
  }
  .capital-chart {
    width: 100%;
    padding: 10px 0;
    height: 350px;
  }
}
.minW{
  width:150px;
}

</style>



